export async function handleAuth(request, env) {
  const url = new URL(request.url);
  const path = url.pathname;

  if (path === '/api/auth/login' && request.method === 'POST') {
    return handleLogin(request, env);
  }
  
  if (path === '/api/auth/register' && request.method === 'POST') {
    return handleRegister(request, env);
  }
  
  if (path === '/api/auth/logout' && request.method === 'POST') {
    return handleLogout(request, env);
  }

  return new Response('Not Found', { status: 404 });
}

async function handleLogin(request, env) {
  try {
    const { username, password } = await request.json();
    
    // Get existing users from Durable Object or use defaults
    const users = await getUsers(env);
    
    if (users[username] && users[username] === password) {
      // Create session token
      const sessionToken = crypto.randomUUID();
      
      // Store session in KV (if using KV storage)
      // await env.SESSIONS.put(sessionToken, JSON.stringify({ username, created: Date.now() }));

      return new Response(JSON.stringify({
        success: true,
        sessionToken,
        username
      }), {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        }
      });
    } else {
      return new Response(JSON.stringify({ error: 'Invalid credentials' }), {
        status: 401,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        }
      });
    }
  } catch (error) {
    return new Response(JSON.stringify({ error: 'Login failed' }), {
      status: 500,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      }
    });
  }
}

async function handleRegister(request, env) {
  try {
    const { username, password } = await request.json();
    
    // Validate input
    if (!username || !password) {
      return new Response(JSON.stringify({ error: 'Username and password are required' }), {
        status: 400,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        }
      });
    }
    
    // Validate username format
    if (!/^[a-zA-Z0-9_]{3,16}$/.test(username)) {
      return new Response(JSON.stringify({ error: 'Username must be 3-16 characters, letters, numbers, and underscores only' }), {
        status: 400,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        }
      });
    }
    
    // Validate password
    if (password.length < 6) {
      return new Response(JSON.stringify({ error: 'Password must be at least 6 characters' }), {
        status: 400,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        }
      });
    }
    
    // Get existing users
    const users = await getUsers(env);
    
    // Check if username already exists
    if (users[username]) {
      return new Response(JSON.stringify({ error: 'Username already exists' }), {
        status: 409,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        }
      });
    }
    
    // Add new user
    users[username] = password;
    await saveUsers(env, users);
    
    return new Response(JSON.stringify({
      success: true,
      message: 'Account created successfully'
    }), {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      }
    });
    
  } catch (error) {
    return new Response(JSON.stringify({ error: 'Registration failed' }), {
      status: 500,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      }
    });
  }
}

async function getUsers(env) {
  try {
    // Try to get users from Durable Object storage
    const durableObjectId = env.CHAT_ROOM.idFromName('user-storage');
    const durableObject = env.CHAT_ROOM.get(durableObjectId);
    
    const response = await durableObject.fetch('http://localhost/users');
    if (response.ok) {
      const data = await response.json();
      return data.users || getDefaultUsers();
    }
  } catch (error) {
    console.error('Error getting users from storage:', error);
  }
  
  // Fallback to default users
  return getDefaultUsers();
}

async function saveUsers(env, users) {
  try {
    // Save users to Durable Object storage
    const durableObjectId = env.CHAT_ROOM.idFromName('user-storage');
    const durableObject = env.CHAT_ROOM.get(durableObjectId);
    
    await durableObject.fetch('http://localhost/users', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ users })
    });
  } catch (error) {
    console.error('Error saving users to storage:', error);
  }
}

function getDefaultUsers() {
  return {
    'parsnip_lover': 'chaos123',
    'falcon_king': 'garden456',
    'chaos_queen': 'parsnip789'
  };
}

async function handleLogout(request, env) {
  // Handle logout logic
  return new Response(JSON.stringify({ success: true }), {
    headers: {
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin': '*'
    }
  });
}
